import React, { Component } from "react";
import style from "./styles/Services.css";
import LineDesign from '../assets/images/LineDesign.png'

export default class Services extends Component {
  render() {
    return (
      <div className={`section Section`}>
        <div className={`${style.aligner}`}>
            <div className={`${style.alignerChild}`}>
                <img src={LineDesign} className={`${style.lineDesign}`}/>
                <h1 className={`Plaak ${style.header}`}>learn. build. repeat.</h1>
                <p className={`MarkProBook ${style.copy}`}>Great Machine helps you embrace <br/> AI with thinking and making</p>
            </div>
        </div>
      </div>
    );
  }
}



