import React, { Component } from "react";
import style from "./styles/Thinking.css";
export default class Thinking extends Component {
  render() {
    return (
      <div className={`section Section ${style.container}`}>
        <div className={style.grid}>
            <div className={style.descriptionContainer}>
            <div>
                <h1 className={`Plaak ${style.header}`}>SERVICES</h1>
                <p className={`MarkProBook ${style.copy}`}>Great Machine helps you embrace AI with thinking and making</p>
            </div>
                {/* ===================== BRAND AI STRATEGY ===================== */}
            <div>
                <h1 className={`Plaak ${style.header}`}>BRAND AI STRATEGY</h1>
                <p className={`MarkProBook ${style.copy} `}>A unifying AI vision for the future of the company. E.g. mission statement, manifesto, vision video.</p>
            </div>
            {/* ===================== AI CORPORATE PLAYBOOK ===================== */}
            <div>
                <h1 className={`Plaak ${style.header}`}>AI CORPORATE PLAYBOOK</h1>
                <p className={`MarkProBook ${style.copy} `}>An actionable plan for how, when, and why to pursue AI projects. E.g. AI 101 whitepapers, action plans, decision trees.</p>
            </div>
            {/* ===================== AI THOUGHT LEADERSHIP ===================== */}
            <div>
                <h1 className={`Plaak ${style.header}`}>AI THOUGHT LEADERSHIP</h1>
                <p className={`MarkProBook ${style.copy} `}>Bring your organization up to speed on the future of AI and Machine Learning. E.g. symposiums, C-Suite presentations, monthly newsletters.</p>
            </div>
            </div>

        </div>

      </div>
    );
  }
}



