import React, { Component } from 'react'
import * as Section from './components/index.js'
import ReactFullPage from './components/Fullpage/fullpage-component'
import './components/styles/App.css'
import 'fullpage.js/vendors/scrolloverflow'

const FullpageWrapper = fullpageProps => (
  <ReactFullPage
  {...fullpageProps}
  render={({ state, fullpageApi }) => {
    return (
      <div>
        <Section.Landing
          section = {fullpageProps.destination.anchor}/>
        <Section.About/>
        <Section.Brands/>
        <Section.Projects
          showVideoPlayer = {fullpageProps.showVideoPlayer}/>
        <Section.Thinking
          section = {fullpageProps.destination.anchor}/>
        <Section.Making
          section = {fullpageProps.destination.anchor}/>     
        <Section.About2020/>
        <Section.Footer/>

      </div>
    );
  }}
/>);

class App extends Component {
  constructor(props){
    super(props)
    this.state = {
      fullpage: {
        callbacks: ['onLeave', 'afterResize'],
        callback: '',
        destination: {
          anchor: 'Home'
        },
        direction: '',
        initialized: false,
        origin: '',
        licenseKey: 'OPEN-SOURCE-GPLV3-LICENSE',
        scrollOverflow: true,
        anchors: ['Home', 'About', 'Brands', 'Projects', 'Thinking', 'Making', 'About2020', 'Contact'],
        returnFullpageObject: this.returnObject.bind(this),
        showVideoPlayer: this.showVideoPlayer.bind(this)
      },
      openNav: false,
      isDesktop: false,
      isTouchDevice: false,
      showVideoPlayer: false,
      link: '',
      blueBackground: false,
      onLanding: true
    }
  }

  componentDidMount(){
    this.deviceCheck()
  }

  // Used in fullpage-component--returns object created in that component.
  returnObject(obj){
    // SetState for app
    this.setState({fullpage: obj})

    // Background State
    if (obj.destination.anchor === 'Brands' || obj.destination.anchor === 'Contact' || obj.destination.anchor === 'Services' || obj.destination.anchor === 'Thinking'){this.setState({blueBackground: true})}
    else {this.setState({blueBackground: false})}

    // Paragraphs in About and About2020
    if (obj.destination.anchor !== 'About' || obj.destination.anchor !== 'About2020') {
      this.setState({shiftRight: false})
    }

    // Show AI
    if (obj.destination.anchor === 'Home' || obj.destination.anchor === 'About') {this.setState({onLanding: true})}
    else {this.setState({onLanding: false})}

  }

  deviceCheck() {
    this.setState({
      isDesktop: window.innerWidth > 768
    })
    this.checkTouchDevice()
  }
p
  checkTouchDevice() {
    if (navigator.userAgent.match(/(iPhone|iPod|iPad|Android|playbook|silk|BlackBerry|BB10|Windows Phone|Tizen|Bada|webOS|IEMobile|Opera Mini)/) !== null) {
      this.setState({
        isTouchDevice: true,
        openNav: false,
      })
    }
    else {this.setState({isTouchDevice: false})}
  }

  toggleNav(){
    this.setState({ openNav: !this.state.openNav })
  }

  showVideoPlayer(link){
    this.setState({
      showVideoPlayer: !this.state.showVideoPlayer,
      link: link
    })
  }

  closeVideoPlayer(){
    this.setState({showVideoPlayer: !this.state.showVideoPlayer})
  }

  render() {
    return (
      <div className={`App backgroundTransition ${this.state.blueBackground ? 'blue': 'black'} ${this.state.onLanding ? 'onLanding' : 'offLanding'} ${this.state.fullpage.destination.anchor === 'Thinking' ? 'showThinking' :
        (this.state.fullpage.destination.anchor === 'Making' ? 'showMaking'  : '')}`}>
        <Section.Navigation
          showLogo={this.state.fullpage.destination.anchor === 'Home' || this.state.fullpage.destination.anchor === 'About' || this.state.fullpage.origin === '' ? true: false}
          toggleNav={this.toggleNav.bind(this)}
          openNav = {this.state.openNav}
          section = {this.state.fullpage.destination.anchor}/>
        <Section.VideoPlayer
          show={this.state.showVideoPlayer}
          close={this.closeVideoPlayer.bind(this)}
          link={this.state.link}/>
        <FullpageWrapper {...this.state.fullpage}/>
      </div>
    );
  }
}

export default App;



